.materials {
  background-color: $c-blue;
  padding-bottom: 60px;
}
/* Style the tab */
.tab {
    text-align: center;
}
.tablink {
  font-size: 24px;
  line-height: 24px;
  color: $c-gold;
  margin-right: 43px;
  cursor: pointer;
  position: relative;
  &.active {
      font-weight: bold;
    &:after {
      content: "";
      position: absolute;
      left:40%;
      top:30px;
      width: 30px;
      height: 3px;
      background: $c-gold;
      border-radius: 2px;
    }
  }
}

/* Style the buttons inside the tab */
.tab button {
  background-color: #ccc;
  float: left;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 14px 16px;
  transition: 0.3s;
  font-size: 17px;
}

/* Change background color of buttons on hover */
.tab button:hover {
  background-color: #ddd;
}

/* Create an active/current tablink class */
.tab button.active {
  background-color: #ccc;
}

/* Style the tab content */
.tabcontent {
  display: none;
  padding: 6px 12px;
  border-top: none;
  color: #fff;
}
#London {
  display: block;
}
